<template>
   <div class="cart">
      <Navbar title="购物车"></Navbar>
      <div class="cart-item" v-for="index in 8" :key="index">
         <div class="left">
            <van-checkbox v-model="checked"></van-checkbox>
         </div>
         
         <van-card
               num="2"
               price="2.00"
               desc=""  
               title="商品标题"
               thumb="http://www.chungold.com/files/default/2017/09-17/16152804176c591432.png"
               origin-price="10.00"
               lazy-load
               >
            
               <div slot="footer" class="footer">
                  <span class="sub" @click="subNum">-</span>
                  <span>{{num}}</span>
                  <span class="add" @click="addNum">+</span> 
               </div>
            </van-card>
      </div>
     

      <van-submit-bar
         :price="3050"
         button-text="提交订单"
         safe-area-inset-bottom
      >
         <van-checkbox v-model="checked">全选</van-checkbox>
      </van-submit-bar>
   </div>
</template>

<script>
import Navbar from '../public/Navbar.vue'
export default {
   data(){
      return {
         num: 1,
         checked: true
      }
   },
   methods: {
      subNum(){
         console.log(6767);
         this.num > 1 ? this.num-- : null;  
      },
      addNum(){
         this.num++;
      },
   },
   components:{
      Navbar
   },
   // beforeRouteEnter (to, from, next) {
   //    if(!localStorage.token){
   //       next({path: '/login'})
   //    }
   // }
}
</script>

<style lang="scss" scoped>
.cart {
   padding-bottom: 250px;
}
.van-submit-bar {
   bottom: 100px;
   padding-left: 30px;
   background: #fff;
   width: 750px;
   left: 50%;
   transform: translateX(-50%);
}
.cart-item {
   padding-left: 30px;
   display: flex;
   justify-content: space-between;
   background: #fff;
   margin-top: 20px;
   .left {
      display: flex;
      height: 282px;
      align-items: center;
   }
   .van-card {
      flex: 1 0 auto; 
      margin-top: 0;
   }
}
.van-card__footer {
   overflow: hidden;
   padding-bottom: 10px;
   padding-top: 20px;
}
.footer {
   width: 150px;
   display: flex;
   border: 1px solid #ebebeb;
   float: right;

   span {
      width: 50px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-right: 1px solid #ebebeb;
      font-size: 28px;
   }
   span:last-child {
      border: 0;
   }
   span:nth-child(2) {
      font-size: 26px;
   }
   span:first-child:active,
   span:last-child:active {
      background: #000;
      color: #fff;
   }
}
</style>


